/* eslint-disable */
/**
 * AES
 * @param {ArrayBuffer|TypedArray} password
 * @param {ArrayBuffer|TypedArray} salt
 * @return {CryptoKey} AES-256-GCM
 */
async function deriveKey (password, salt) {
  const passwordKey = await window.crypto.subtle.importKey(
      'raw',
      password,
      'PBKDF2',
      false,
      ['deriveKey']
  );

  return await window.crypto.subtle.deriveKey(
      {
        name: 'PBKDF2',
        salt: salt,
        iterations: 2000,
        hash: 'SHA-256'
      },
      passwordKey,
      {
        name: 'AES-GCM',
        length: 256
      },
      true,
      ['encrypt', 'decrypt']
  );
}

/**
 * 
 * @param {string} message 
 * @param {string} password 
 * @return {{cipher: ArrayBuffer, iv: Uint8Array, salt: Uint8Array}} 
 */
async function encrypt (message, password) {
  // TypedArray
  const pwd = new TextEncoder().encode(password);

  // salt
  const salt = window.crypto.getRandomValues(new Uint8Array(16));

  // salt
  const key = await deriveKey(pwd, salt);

  // 
  const iv = window.crypto.getRandomValues(new Uint8Array(12));

  // 
  const cipher = await window.crypto.subtle.encrypt(
    {
      name: 'AES-GCM',
      iv: iv,
      tagLength: 128, // GCM
      //  GCMAAD
      additionalData: new TextEncoder().encode('test')
    },
    key,
    new TextEncoder().encode(message)
  );

  // IV, salt, 
  return {
    iv,
    salt,
    cipher,
  };
}


/**
 * saltIV
 * @param {{iv: Uint8Array, salt: Uint8Array}}  iv, salt
 * @param {ArrayBuffer} cipher 
 * @param {string} password 
 * @return {string} 
 */
async function decrypt({iv, salt}, cipher, password) {
  // TypedArray
  const pwd = new TextEncoder().encode(password);

  // salt
  const key = await deriveKey(pwd, salt);

  // 
  const buffer = await window.crypto.subtle.decrypt(
      {
        name: 'AES-GCM',
        iv: iv,
        tagLength: 128,
        additionalData: new TextEncoder().encode('test')
      },
      key,
      cipher
  );

  return new TextDecoder().decode(buffer);
}

export {
  encrypt, decrypt
};

