/**
 *  POST 
 */
class HttpPoster {
  /**
   * POST 
   * @param {string} endpoint POST 
   */
  constructor(endpoint) {
    this.endpoint = endpoint;
  }

  /**
   * POST 
   */
  static RESULT = {
    CANCELED: {
      ok: false,
      result: 'CANCELED',
      resultcode: -1,
    },
    AGREED: {
      ok: true,
      result: 'AGREED',
      resultcode: 1,
    },
  };

  /**
   * JSON 
   * @param {object} result  HttpPoster.RESULT 
   * @param {object|null} [payload=null] 
   * @returns {boolean} fetch 
   */
  async postWithJSON(result, payload = null) {
    try {
      const body = result;
      console.log('body', body);
      if (payload) Object.assign(body, payload);
      console.log('payload', payload);
      if (!body) throw new Error(' resultName ');
      const response = await fetch(this.endpoint, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(body),
      });
      return response.ok;
    } catch (error) {
      console.error(error);
      return false;
    }
  }
}
